//====================================



svg.atom {
  max-height: 100vh;
  max-width: 100vw;
  overflow: visible;
}
svg circle.kern {
  fill: #FFEA00;
  stroke: none;
  filter: drop-shadow(0px 0px 4px #aeea00);
}
.atom path {
  fill: none;
  stroke: #fff;
  stroke-width: .06;
  stroke-width: .12;
}
/* 129 = ellipse.getTotalLength() */
path.tail {
  stroke: #bda800;
  fill: none;
  animation: atom 1.5s infinite linear;
  stroke-dashoffset: 0;
  stroke-dasharray: 20,44.5;
  stroke-width: .6;
}
path.electron {
  stroke: #FFEA00;
  fill: none;
  animation: atom 1.5s infinite linear;
  stroke-dashoffset: 0;
  stroke-dasharray: .1,64.4;
  stroke-width: 2;
  stroke-linecap: round;
}
path.tail1,
path.electron1 {
  animation-delay: -1s;
}
path.tail2,
path.electron2 {
  animation-delay: -1.4s;
}
/* 129 = ellipse.getTotalLength() */
@keyframes atom {
  to {
    stroke-dashoffset: 129;
  }
}